import React, { Component } from 'react';
import './ConsultHistory.css';
import { withStyles } from '@material-ui/core/styles';
import { AppBar, Button, Snackbar, Toolbar, Typography, Dialog, CardHeader, Avatar, Divider } from "@material-ui/core";
import { downloadConsultFile, getConsultFiles, getConsultInfo, getConsultNotes } from "../../ConsultApiActions";
import { connect } from "react-redux";
import MessageModal from "../../../../utils/components/MessageModal/MessageModal";
import { Redirect } from "react-router";
import Constants from '../../../../utils/Constants';
import ConstantMessages from '../../../../utils/Strings';
import { clearSnackbar, showSnackbar } from "../../../../utils/components/AppLoader/AppLoaderActions";
import { Alert } from "@material-ui/lab";
import Card from "@material-ui/core/Card";
import Grid from "@material-ui/core/Grid";
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import MuiDialogContent from '@material-ui/core/DialogContent';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import moment from 'moment';
import SingleFile from "../Document/SingleFile";

import logo from '../../../../assets/svg/header_logo.png';

function getBase64(file) {
  return new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = () => resolve(reader.result);
    reader.onerror = error => reject(error);
  });
}

const styles = (theme) => ({
  root: {
    margin: 0,
    padding: theme.spacing(2),
  },
  closeButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
    color: theme.palette.grey[500],
  },
  imageLogo: {
    width: '100px',
    height: '45px'
  },
  large: {
    width: theme.spacing(7),
    height: theme.spacing(7),
  }
});

const DialogTitle = withStyles(styles)((props) => {
  const { children, classes, onClose, ...other } = props;
  return (
    <MuiDialogTitle disableTypography className={classes.root} {...other}>
      <Typography variant="h6">{children}</Typography>
      {onClose ? (
        <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
          <CloseIcon />
        </IconButton>
      ) : null}
    </MuiDialogTitle>
  );
});

const DialogContent = withStyles((theme) => ({
  root: {
    padding: theme.spacing(0),
  },
}))(MuiDialogContent);

class ConsultHistory extends Component {
  constructor(props) {
    super(props);

    this.state = {
      consult: {},
      soapNote: null,
      files: null,
      previewVisible: false,
      previewImage: null,
      videoDialog: false,
      previewRecording: null
    };
  }

  componentDidMount() {
    this.getConsultInformation();
    this.getSoapNote();
    this.getDocuments();
  }

  getConsultInformation = () => {
    const consultId = sessionStorage.getItem('consultHistory');
    if (consultId != null) {
      this.props.getConsultInfo(consultId).then(res => {
        this.setState({ consult: res.data[0] });
      }, error => {
        this.props.showSnackbar(ConstantMessages.somethingWentWrong, Constants.SNACKBAR_VARIAINT.ERROR);
      });
    }
  };

  getSoapNote = () => {
    const consultId = sessionStorage.getItem('consultHistory');
    if (consultId != null) {
      this.props.getSoapNote(consultId).then(res => {
        this.setState({ soapNote: res.data });
      }, error => {
        this.props.showSnackbar(ConstantMessages.somethingWentWrong, Constants.SNACKBAR_VARIAINT.ERROR);
      });
    }
  };

  getDocuments = () => {
    const consultId = sessionStorage.getItem('consultHistory');
    if (consultId != null) {
      this.props.getDocuments(consultId).then(res => {
        this.setState({ files: res.data });
      }, error => {
        this.props.showSnackbar(ConstantMessages.somethingWentWrong, Constants.SNACKBAR_VARIAINT.ERROR);
      });
    }
  };

  handlePreview = fileId => {
    const previewImage = process.env.REACT_APP_CONSULT_SERVER_API_URL + `/consult/consultFiles/${fileId}`;
    this.setState({ previewVisible: true, previewImage: previewImage });
  };

  /**
   * Method used to close the snackbar
   */
  handleCloseSnackbar = () => {
    this.props.clearSnackbar();
  };

  handleCancel = () => {
    this.setState({ previewVisible: false, previewImage: '' });
  };

  handleCloseVideoDialog = () =>{
    this.setState({ videoDialog: false, previewRecording: '' })
  }

  videoDialog = (e, recording) => {
    this.setState({
      videoDialog: true,
      previewRecording: recording
    })
  }

  cardTitleRender = () => {
    return (
      <Grid container spacing={3}>
        <Grid item xs={12} sm={4} className="cardHeaderGridItem">
          Patient Information
        </Grid>
        <Grid item xs={12} sm={4} className="cardHeaderGridItem">
          Recording
        </Grid>
        <Grid item xs={12} sm={4} className="cardHeaderGridItem">
          Attachments
        </Grid>
      </Grid>
    )
  }

  render() {
    const consultId = sessionStorage.getItem('consultHistory');
    const { classes } = this.props;
    const { consult, soapNote, files, previewVisible, previewImage, videoDialog, previewRecording } = this.state;
    if(consult && consult.patientDetails){
      var arrayName = consult.patientDetails.name.split(' ');
      var avartarName = '';
      for(var x = 0; x < arrayName.length; x++){
        if(x < 3){
          avartarName=avartarName+arrayName[x].charAt(0).toUpperCase();
        }
      }
    }else{}
    
    if (consultId === null) {
      return <Redirect to={'/dashboard'} />
    }
    return (
      <>
        <AppBar
          color="inherit"
          position="fixed">
          <Toolbar>
            <img
              alt="Logo"
              src={logo}
              className={classes.imageLogo}
            />
            <Button variant="contained" color="secondary" className={'end-consult-button'}
              onClick={() => this.props.history.goBack()}>Close</Button>
          </Toolbar>
        </AppBar>
        <main className={'history-consult-content'}>
          <div className={'section-header'}>Patient Visit History</div>
          {
            consult.patientDetails &&
            <Card className={'information-card'}>
              <CardHeader className="cardHeader" title={ this.cardTitleRender() } />
                <Grid container spacing={1}>
                  <Grid item xs={12} sm={4} className="patientInformationContainer">
                    <Grid container spacing={3}>
                      <Grid item xs={12} sm={2}>
                        <Avatar className={classes.large}>{avartarName}</Avatar>
                      </Grid>
                      <Grid item xs={12} sm={10}>
                        <div className="patient-name">{consult.patientDetails.name}</div>
                        <div>Appointment Time: {consult.appointmentTime}</div>
                        <div>Start Time: {consult.consultStartTime}</div>
                        <div>End Time: {consult.consultEndTime}</div>
                      </Grid>
                    </Grid>
                  </Grid>
                  <Grid item xs={12} sm={4} className="recordingContainer">
                    <Grid container spacing={3}>
                      {
                        consult.recordings && consult.recordings.map((recording, index) => (
                          <Grid key={recording.id} item xs={4}>
                            <Button 
                              size="small"
                              name={recording.id} 
                              className="showRecordingButton" 
                              variant="contained" color="primary"
                              onClick={(e) => this.videoDialog(e,recording)}
                            > 
                              Recording { index+1 }
                            </Button>
                          </Grid>
                        ))
                      }
                    </Grid>
                  </Grid>
                  <Grid item xs={12} sm={4} className="attachmentsContainer">
                      <Grid container spacing={1}>
                        {files && files.map(file => (
                          <Grid key={file.fileId} item xs={4}>
                            <SingleFile file={file} handleRemove={this.handleRemove}
                              handlePreview={this.handlePreview} userId={null} />
                          </Grid>
                        ))}
                        {
                          files && files.length === 0 &&
                          <div>Documents not available</div>
                        }
                      </Grid>
                  </Grid>
                </Grid>
                
                <Divider />
                
                <Grid container spacing={1} className="reasonForVisitContainer">
                  <Grid item xs={12} sm={12} className="reasonTitle">
                        Reason for Visit
                  </Grid>
                  <Grid item xs={12} sm={12} className="reasonDetails">
                        {consult.patientDetails.chiefComplaint}
                  </Grid>
                </Grid>

                <Grid container spacing={1} className="notesContainer">
                  <Grid item xs={12} sm={12} className="notesTitle">
                        Diagnosis Notes
                  </Grid>
                  <Grid item xs={12} sm={12} className="notesDetails">
                        {
                          soapNote
                          ?<div>{soapNote.plan}</div>
                          :<div>Notes not available</div>
                        }
                  </Grid>
                </Grid>
            </Card>
          }

          <Snackbar open={this.props.showSnackbar} autoHideDuration={3000}
            onClose={() => this.handleCloseSnackbar()}
            anchorOrigin={{ vertical: 'top', horizontal: 'right' }}>
            <Alert severity={this.props.snackbarVariant}>
              {this.props.snackbarMessage}
            </Alert>
          </Snackbar>

          <Dialog aria-labelledby="customized-dialog-title" open={previewVisible} maxWidth="md" fullWidth={true}>
            <DialogTitle id="customized-dialog-title" onClose={this.handleCancel}>
              Preview
            </DialogTitle>
            <DialogContent dividers>
              <img alt="example" style={{ width: '100%' }} src={previewImage} />
            </DialogContent>
          </Dialog>

          <Dialog aria-labelledby="customized-dialog-title" open={videoDialog}>
            <DialogTitle id="customized-dialog-title" onClose={this.handleCloseVideoDialog}>
              Recording
            </DialogTitle>
            <DialogContent dividers>
              <video 
                width="500" height="400" 
                controls src={previewRecording?previewRecording.url:null} 
                className={'video-recording'} 
              />
            </DialogContent>
          </Dialog>
        </main>

        <MessageModal
          ref="messageModal"
        />
      </>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    showSnackbar: state.showLoader.showSnackbar,
    snackbarVariant: state.showLoader.snackbarVariant,
    snackbarMessage: state.showLoader.snackbarMessage
  }
};

const mapDispatchToProps = (dispatch) => {
  return {
    getConsultInfo: consultId => {
      return dispatch(getConsultInfo(consultId)).then(res => {
        return Promise.resolve(res);
      }, error => {
        return Promise.reject(error);
      });
    },

    getSoapNote: consultId => {
      return dispatch(getConsultNotes(consultId)).then(res => {
        return Promise.resolve(res);
      }, error => {
        return Promise.reject(error);
      });
    },
    getDocuments: consultId => {
      return dispatch(getConsultFiles(consultId)).then(res => {
        return Promise.resolve(res);
      }, error => {
        return Promise.reject(error);
      });
    },
    downloadConsultFile: consultId => {
      return dispatch(downloadConsultFile(consultId)).then(res => {
        return Promise.resolve(res);
      }, error => {
        return Promise.reject(error);
      });
    },
    showSnackbar: (message, variant) => {
      dispatch(showSnackbar(message, variant));
    },
    clearSnackbar: () => {
      dispatch(clearSnackbar())
    },
  }
};

ConsultHistory = connect(
  mapStateToProps,
  mapDispatchToProps
)(ConsultHistory);

export default withStyles(styles)(ConsultHistory);
