import React, { forwardRef } from 'react'
import { connect } from 'react-redux';
import { NavLink as RouterLink } from 'react-router-dom';
import clsx from 'clsx';
import PropTypes from 'prop-types';

import { withStyles } from '@material-ui/core/styles';
import { List, ListItem, Button, colors, Collapse } from '@material-ui/core';

import Constants from '../../../Constants';

import DashboardIcon from '@material-ui/icons/Dashboard';
import PeopleIcon from '@material-ui/icons/People';
import AccountBoxIcon from '@material-ui/icons/AccountBox';
import SettingsIcon from '@material-ui/icons/Settings';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';

import InboxIcon from '@material-ui/icons/MoveToInbox';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';
import StarBorder from '@material-ui/icons/StarBorder';

import HistoricalConsultIcon from '@material-ui/icons/AssignmentTurnedInRounded';
import RequestQueyeIcon from '@material-ui/icons/AssignmentIndRounded';
import UpcomingConsultIcon from '@material-ui/icons/AssignmentReturnedRounded';
import AccountIcon from '@material-ui/icons/AccountBalanceWalletRounded';

const CustomRouterLink = forwardRef((props, ref) => {
    return (
        <div
            ref={ref}
            style={{ flexGrow: 1 }}
        >
            <RouterLink {...props} />
        </div>
    )
});

const styles = theme => ({
    root: {},
    item: {
        display: 'flex',
        paddingTop: 0,
        paddingBottom: 0
    },
    button: {
        color: colors.blueGrey[800],
        padding: '10px 8px',
        justifyContent: 'flex-start',
        textTransform: 'none',
        letterSpacing: 0,
        width: '100%',
        fontWeight: theme.typography.fontWeightMedium
    },
    icon: {
        color: theme.palette.icon,
        width: 24,
        height: 24,
        display: 'flex',
        alignItems: 'center',
        marginRight: theme.spacing(1)
    },
    active: {
        color: theme.palette.primary.main,
        fontWeight: theme.typography.fontWeightMedium,
        '& $icon': {
            color: theme.palette.primary.main
        }
    },
    nested: {
        paddingLeft: theme.spacing(4),
    }
});

class SidebarNav extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            open: false
        }
    }

    handleClick = () => {
        this.setState({ open: !this.state.open })
    };

    render() {
        const { classes, className, loggedInUser, loggedInUserPractice } = this.props;
        const { isPracticeAdmin, isDoctor, isCoordinator } = this.props.currentRole;
        return (
            <List
                className={clsx(classes.root, className)}
            >
                <ListItem className={classes.item} disableGutters onClick={() => this.props.checkOnClick(Constants.ACTIONS.DASHBOARD)}>
                    <Button activeClassName={classes.active} to={Constants.ACTIONS.DASHBOARD}
                        className={classes.button} component={CustomRouterLink}>
                        <div className={classes.icon}><DashboardIcon /></div>
                        Dashboard
                    </Button>
                </ListItem>

                {
                    isPracticeAdmin || isCoordinator
                        ? (
                            <ListItem className={classes.item} disableGutters onClick={() => this.props.checkOnClick(Constants.ACTIONS.USER_LIST)}>
                                <Button activeClassName={classes.active} to={Constants.ACTIONS.USER_LIST}
                                    className={classes.button} component={CustomRouterLink}>
                                    <div className={classes.icon}><PeopleIcon /></div>
                                    Users
                            </Button>
                            </ListItem>
                        )
                        : <></>
                }

                {
                    isDoctor || isCoordinator
                        ? (
                            <>
                                <ListItem className={classes.item} onClick={this.handleClick} disableGutters>
                                    <Button className={classes.button}>
                                        <div className={classes.icon}><InboxIcon /></div>
                                        Consults
                                    <span className="iconSetRightPosition">{this.state.open ? <ExpandLess /> : <ExpandMore />}</span>
                                    </Button>
                                </ListItem>
                                <Collapse in={this.state.open} timeout="auto" unmountOnExit>
                                    <List component="div" disablePadding>
                                        <ListItem className="subMenu" onClick={() => this.props.checkOnClick(Constants.ACTIONS.UPCOMING_CONSULTS)}>
                                            <Button activeClassName={classes.active} to={Constants.ACTIONS.UPCOMING_CONSULTS}
                                                className={classes.button} component={CustomRouterLink}>
                                                <div className={classes.icon}><UpcomingConsultIcon /></div>
                                                Upcoming
                                        </Button>
                                        </ListItem>
                                        <ListItem className="subMenu" onClick={() => this.props.checkOnClick(Constants.ACTIONS.HISTORICAL_CONSULTS)}>
                                            <Button activeClassName={classes.active} to={Constants.ACTIONS.HISTORICAL_CONSULTS}
                                                className={classes.button} component={CustomRouterLink}>
                                                <div className={classes.icon}><HistoricalConsultIcon /></div>
                                                Historical
                                        </Button>
                                        </ListItem>
                                    </List>
                                </Collapse>
                            </>
                        )
                        : <></>
                }

                {
                    isPracticeAdmin
                        ? (
                            <>
                                {
                                    loggedInUser.isSubscription || loggedInUserPractice.Practice.allowPayment
                                        ? (
                                            <ListItem className={classes.item} disableGutters onClick={() => this.props.checkOnClick(Constants.ACTIONS.ACCOUNT)}>
                                                <Button activeClassName={classes.active} to={Constants.ACTIONS.ACCOUNT}
                                                    className={classes.button} component={CustomRouterLink}>
                                                    <div className={classes.icon}><AccountIcon /></div>
                                                    Account
                                        </Button>
                                            </ListItem>
                                        )
                                        : <></>
                                }
                                <ListItem className={classes.item} disableGutters onClick={() => this.props.checkOnClick(Constants.ACTIONS.PRACTICE_PROFILE_VIEW)}>
                                    <Button activeClassName={classes.active} to={Constants.ACTIONS.PRACTICE_PROFILE_VIEW}
                                        className={classes.button} component={CustomRouterLink}>
                                        <div className={classes.icon}><SettingsIcon /></div>
                                        Practice Profile
                                </Button>
                                </ListItem>
                            </>
                        )
                        : <></>
                }

                <ListItem className={classes.item} disableGutters onClick={() => this.props.checkOnClick(Constants.ACTIONS.PROFILE_VIEW)}>
                    <Button activeClassName={classes.active} to={Constants.ACTIONS.PROFILE_VIEW}
                        className={classes.button} component={CustomRouterLink}>
                        <div className={classes.icon}><AccountBoxIcon /></div>
                        Profile
                    </Button>
                </ListItem>

                <ListItem className={classes.item} disableGutters>
                    <Button onClick={() => this.props.logoutUser()}
                        className={classes.button}>
                        <div className={classes.icon}><ExitToAppIcon /></div>
                        Logout
                    </Button>
                </ListItem>
            </List>
        )
    }

}

SidebarNav.propTypes = {
    className: PropTypes.string
};

const mapStateToProps = state => {
    return {
        loggedInUser: state.auth.loggedInUser,
        loggedInUserPractice: state.auth.loggedInUserPractice,
        currentRole: state.auth.currentRole,
    }
};

const mapDispatchToProps = (dispatch, ownProps) => {
    return {}
};

SidebarNav = connect(
    mapStateToProps,
    mapDispatchToProps
)(SidebarNav);

export default withStyles(styles)(SidebarNav);