import React from 'react';
import MaterialTable from 'material-table';
import { connect } from 'react-redux';

import { Grid, Paper, Chip, Tooltip, Avatar } from '@material-ui/core';
import EditIcon from '@material-ui/icons/EditRounded';
import { withStyles } from '@material-ui/core/styles';

import {
  addUserRequest,
  inviteUserRequest,
  getAllUsersRequest,
  updateUserRequest,
} from '../../UserApiActions';
import { signOut } from '../../../Authentication/AuthenticationActions';
import AddUser from '../AddUser/AddUser';
import { setPageTitle } from "../../../Dashboard/DashboardActions";

import Constants from '../../../../utils/Constants';
import './UserTable.css';
import AppLoader from "../../../../utils/components/AppLoader/AppLoader";
import MessageModal from "../../../../utils/components/MessageModal/MessageModal";

import PhoneIcon from '../../../../assets/svg/phone.svg';
import EmailIcon from '../../../../assets/svg/email.svg';

const styles = theme => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
    color: theme.palette.text.secondary,
  },
  chips: {
    display: 'flex',
    flexWrap: 'wrap',
  },
  chip: {
    margin: 2,
  },
  large: {
    width: theme.spacing(7),
    height: theme.spacing(7),
  }
});

/**
 * User table component which shows all users data
 */
class UserTable extends React.Component {

  /**
   * Change page title when component is render
   * Get all users when component is render
   * AttachComponent method call when component is render
   */
  componentDidMount = () => {
    let data = this.state.paginationParams;
    if (this.props.loggedInUserPractice) {
      data.PracticeId = this.props.loggedInUserPractice.PracticeId;
      this.props.getAllUsers(data).then(() => {
      }, (error) => {
      });
    }

    this.props.updatePageTitle(this.props.pageTitle, false);
  };

  /**
   * Get all users when component receive new props
   * AttachComponent method call when component receive new props
   */
  componentWillReceiveProps = (nextProps) => {
    if (nextProps.loggedInUserPractice && this.props.loggedInUserPractice !== nextProps.loggedInUserPractice) {
      let data = this.state.paginationParams;
      data.PracticeId = nextProps.loggedInUserPractice.PracticeId;
      this.props.getAllUsers(data).then(() => {
      }, (error) => {
      });
    }

    if (this.props.users !== nextProps.users) {
      this.setState({ users: nextProps.users })
    }
  }

  /**
   * Method used for invite new user
   */
  handleInviteUser = (user) => {
    const practiceId = this.props.loggedInUserPractice.PracticeId;
    return new Promise((resolve, reject) => {
      this.props.inviteUser(practiceId, user).then((response) => {
        this.getAllUsers();
        resolve(response);
      }, (error) => {
        reject(error);
      });



    });
  };

  /**
   * Method used for update user details
   */
  handleUpdateUser = (userId, user) => {
    const loggedInUserId = this.props.loggedInUser.id;
    return new Promise((resolve, reject) => {
      this.props.updateUser(userId, user).then((response) => {
        if (userId === loggedInUserId) {
          this.logoutUser();
        } else {
          this.getAllUsers();
          resolve(response);
        }
      }, (error) => {
        reject(error);
      });


    });
  };

  /**
   * Method used for logout logged in user when logged im user update own information
   */
  logoutUser = () => {
    this.refs.messageModal.setState({
      showModal: true, message: 'Your information should update successfully, please login again.',
      title: 'Log out',
      showOkButton: true,
      handleOk: this.props.signOut,
      handleCancel: () => {
      },
      okText: 'Ok'
    });
  };

  /**
   * Method used to get all users
   */
  getAllUsers = () => {
    let data = this.state.paginationParams;
    data.PracticeId = this.props.loggedInUserPractice.PracticeId;
    this.props.getAllUsers(data).then(() => {
    }, (error) => {
    });
  }

  /**
   * Method used for handle edit user details functionlity
   */
  onEditUser = (data) => {
    this.setState({
      isUpdate: true,
      userDetails: data
    })
  }

  /**
   * Method used for cancel edit user details functionality
   */
  cancleEditUser = () => {
    this.setState({
      isUpdate: false,
      userDetails: {}
    }, () => {
      this.getAllUsers();
    })
  }

  constructor(props) {
    super(props);
    this.paginationParams = {
      page: 0,
      rowsPerPage: Constants.ROW_PER_PAGE,
      role: Constants.SYSTEM_ROLES.ROLE_PRACTICE_ADMIN
    };
    this.state = {
      userId: '',
      users: undefined,
      showLoader: false,
      isUpdate: false,
      userDetails: {},
      paginationParams: Object.assign({}, this.paginationParams)
    }
  }

  render() {
    const { classes, loggedInUserPractice } = this.props;
    const { users, paginationParams } = this.state;

    const columns = [{
      title: 'Clinician Information',
      field: 'displayName',
      filterable: false,
      sorting: false,
      render: props => {
        var array1 = props.displayName.split(' ');
        var newarray1 = '';
        for(var x = 0; x < array1.length; x++){
          if(x < 3){
            newarray1=newarray1+array1[x].charAt(0).toUpperCase();
          }
        }
        return (
          <>
            <Grid container spacing={1}>
              <Grid item xs={2}>
                <Avatar>{ newarray1 }</Avatar>
              </Grid>
              <Grid item xs={10}>
                <div><b>{ props.displayName }</b></div>
                <div>
                  {
                    props.DoctorSpecialities.length > 0
                    ?(
                      props.DoctorSpecialities.map((DoctorSpecialitie) => {
                        return <div key={DoctorSpecialitie.speciality} className="" size="small" label={DoctorSpecialitie.speciality}>{ DoctorSpecialitie.speciality }</div>
                      })
                    )
                    :<></>

                  }
                </div>
              </Grid>
            </Grid>
          </>
        )
      }
    }, {
      title: 'Contact Details',
      field: 'email',
      filterable: false,
      sorting: false,
      render: props => {
        return (
          <>
            <div className="contactDetails"><img className="contactIcons" src={EmailIcon}/>{ props.email }</div>
            <div className="contactDetails"><img className="contactIcons" src={PhoneIcon}/>{ props.phone }</div>
          </>
        )
      }
    }, {
      title: 'Role',
      field: 'humanReadableRoles',
      filterable: false,
      sorting: false,
      render: props => {
        return props.humanReadableRoles.map((humanReadableRole) => {
          return <Chip key={humanReadableRole} className="roleChips" size="small" label={humanReadableRole} />
        })
      }
    }, {
      title: 'Fee',
      width: 150,
      field: 'consultationFee',
      hidden: loggedInUserPractice.Practice.allowPayment,
      filterable: false,
      sorting: false,
      render: props => {
        if(props.consultationFee){
          return (
            <>
              <span className="practiceCurrency">{ props.Practice.currency }</span>
              <span>{ props.consultationFee }</span>
            </>
          )
        }else{
          return "-"
        }
      }
    },
    {
      title: 'Action',
      width: 150,
      filterable: false,
      resizable: false,
      field: 'id',
      headerStyle: {
        textAlign: 'center',
      },
      sortable: false,
      sorting: false,
      render: props => {
        return (
          <div className="action-column-cell">
            <Tooltip placement="top" title="Edit user details">
              <EditIcon className="edit-user-icon" onClick={() => this.onEditUser(props)} />
            </Tooltip>
          </div>
        )
      }
    }];

    return (
      <div className="">
        <Grid container>
          <Grid item xs={12}>
            <Grid xs={12} item className="materialUiTableContainer">
              {
                users &&
                <MaterialTable
                  title={
                    <AddUser
                      isUpdate={this.state.isUpdate}
                      userDetails={this.state.userDetails}
                      buttonName={this.props.buttonName}
                      formTitle={this.props.formTitle}
                      handleInviteUser={this.handleInviteUser}
                      cancleEditUser={this.cancleEditUser}
                      onSkipAddUser={this.props.onSkip ? () => this.props.onSkip() : undefined}
                      prevButtonClick={this.props.prevButtonClick ? () => this.props.prevButtonClick() : undefined}
                      handleUpdateUser={this.handleUpdateUser} />
                  }
                  scrollable
                  columns={columns}
                  data={users.rows}
                  options={{
                    search: false,
                    pageSizeOptions: [10, 20, 30, 40, 50],
                    pageSize: paginationParams.rowsPerPage,
                    emptyRowsWhenPaging: false,
                    filtering: false,
                    headerStyle: {
                      padding: '2px 40px 2px 16px'
                    },
                    searchFieldStyle: {
                      padding: '2px 40px 2px 16px'
                    }
                  }}
                  pages={Math.ceil(users.count / paginationParams.rowsPerPage)}
                  totalCount={users.count}
                  page={paginationParams.page}
                  onChangeRowsPerPage={(pageSize) => {
                    let data = this.state.paginationParams;
                    data['rowsPerPage'] = pageSize;
                    this.setState({
                      paginationParams: data
                    }, () => {
                      this.props.getAllUsers(data);
                    })
                  }}
                  onChangePage={(page) => {
                    let data = this.state.paginationParams;
                    data['page'] = page;
                    this.setState({
                      paginationParams: data
                    }, () => {
                      this.props.getAllUsers(this.state.paginationParams);
                    })

                  }}
                />
              }
            </Grid>

            <AppLoader showLoader={this.state.showLoader} />

            <MessageModal
              ref="messageModal" />
          </Grid>
        </Grid>

      </div>
    );
  }

}

const mapStateToProps = (state) => {
  return {
    users: state.user.users,
    loggedInUser: state.auth.loggedInUser,
    currentRole: state.auth.currentRole,
    loggedInUserPractice: state.auth.loggedInUserPractice
  }
};

const mapDispatchToProps = (dispatch) => {
  return {
    getAllUsers: (data) => {
      return dispatch(getAllUsersRequest(data)).then((res) => {
        return Promise.resolve(res);
      }, (error) => {
        return Promise.reject(error);
      });
    },

    addUser: (data) => {
      return dispatch(addUserRequest(data)).then((res) => {
        return Promise.resolve(res);
      }, (error) => {
        return Promise.reject(error);
      });
    },

    inviteUser: (practiceId, data) => {
      return dispatch(inviteUserRequest(practiceId, data)).then((res) => {
        return Promise.resolve(res);
      }, (error) => {
        return Promise.reject(error);
      });
    },

    updateUser: (userId, data) => {
      return dispatch(updateUserRequest(userId, data)).then((res) => {
        return Promise.resolve(res);
      }, (error) => {
        return Promise.reject(error);
      });
    },


    signOut: () => {
      dispatch(signOut())
    },
    updatePageTitle: (pageTitle, enablePageTitle) => {
      dispatch(setPageTitle(pageTitle, enablePageTitle));
    }
  }
};

UserTable = connect(
  mapStateToProps,
  mapDispatchToProps
)(UserTable);

export default withStyles(styles)(UserTable);
