import React from 'react';
import './Footer.css';
import {withStyles} from '@material-ui/core/styles';
import CopyrightIcon from '../../../assets/svg/copyright.png';

import BottomNavigation from '@material-ui/core/BottomNavigation';
import BottomNavigationAction from '@material-ui/core/BottomNavigationAction';

import Dialog from '@material-ui/core/Dialog';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Slide from '@material-ui/core/Slide';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';

import PrivacyPolicy from '../PrivacyPolicy/PrivacyPolicy';
import TermsOfUsage from '../TermsOfUsage/TermsOfUsage';

import HeaderLogo from '../../../assets/svg/header_logo.png'

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

const styles = (theme) => ({
  root: {
    width: 500,
  },
  appBar: {
    position: 'relative',
  },
  title: {
    marginLeft: theme.spacing(2),
    flex: 1,
    textAlign: 'center'
  },
});

class Footer extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      openDialog: false,
      componentRender: undefined
    }
  }

  labelRender = () => {
    var currentDate = new Date();
    var currentYear = currentDate.getFullYear();
    return (
      <>
        <img className="copyrightIcon" src={CopyrightIcon}/>
        <span className="copyrightText">FIGmd inc. {currentYear}, USA. All rights reserved.</span>
      </>
    )
  };

  openDialog = (componentName) => {
    this.setState({openDialog: true, componentRender: componentName})
  };

  closeDialog = () => {
    this.setState({openDialog: false, componentRender: undefined})
  };

  render() {
    const {classes} = this.props;
    const {openDialog, componentRender} = this.state;

    return (
      <>
        <BottomNavigation
          showLabels
          className={`${classes.root} footerContainer`}
        >
          <BottomNavigationAction className="privacyAction" label="Privacy" onClick={() => this.openDialog("Privacy")}/>
          <BottomNavigationAction className="termsAction" label="Terms" onClick={() => this.openDialog("Terms")}/>
          <BottomNavigationAction className="copyrightAction" label={this.labelRender()}/>
        </BottomNavigation>

        <Dialog fullScreen open={openDialog} TransitionComponent={Transition}>
          <AppBar color="inherit" className={classes.appBar}>
            <Toolbar>
              <img src={HeaderLogo}/>
              <Typography variant="h6" className={classes.title}>
                {componentRender}
              </Typography>
              <IconButton edge="start" color="inherit" onClick={this.closeDialog} aria-label="close">
                <CloseIcon/>
              </IconButton>
            </Toolbar>
          </AppBar>
          {
            componentRender
              ? (
                componentRender === 'Privacy'
                  ? (
                    <PrivacyPolicy/>
                  )
                  : componentRender === 'Terms'
                  ? (
                    <TermsOfUsage/>
                  )
                  : <></>
              )
              : <></>
          }
        </Dialog>
      </>
    )
  }

}

export default withStyles(styles)(Footer);
