import React from "react";
import DeleteOutlined from "@material-ui/icons/DeleteOutlined";
import Visibility from "@material-ui/icons/Visibility";
import IconDownload from "@material-ui/icons/GetApp";
import {IconButton, Button} from "@material-ui/core";
import './Document.css';
import DownloadFileRequest from '../../../../utils/DownloadFileAxiosRequest';

import SvgIcon from '@material-ui/core/SvgIcon';
import { ReactComponent as ImageIcon } from '../../../../assets/svg/imageIcon.svg';
import { ReactComponent as PDFIcon } from '../../../../assets/svg/pdfIcon.svg';

export default class SingleFile extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      hover: false
    };
  }

  onMouseOver = () => {
    this.setState({hover: true});
  };

  onMouseOut = () => {
    this.setState({hover: false});
  };

  downloadFile = (fileURL, fileName) => {
    new DownloadFileRequest(fileURL, fileName).send();
  }

  render() {
    const {file, userId} = this.props;
    if(file.id) {
      file.fileId = file.id;
    }

    return (
      <div onMouseEnter={() => this.onMouseOver()} onMouseLeave={() => this.onMouseOut()}>
        <Button fullWidth variant="outlined" color="default">
          <span className={'file-name'}>{file.name}</span>
          <SvgIcon className="imageSize">
            {
              file.fileType && file.fileType.toLowerCase() === '.pdf'
              ?<PDFIcon  color="primary" />
              :<ImageIcon color="primary" />
            }    
          </SvgIcon>


          {this.state.hover && <div className={'file-overlay'}>
          {
            file.fileType && file.fileType.toLowerCase() === '.pdf'
              ? (
              <IconButton aria-label="download" download={file.name} className={'document-icon-button'}>
                <IconDownload onClick={ () => this.downloadFile(process.env.REACT_APP_CONSULT_SERVER_API_URL + `/consult/consultFiles/${file.fileId}`, file.name) }/>
              </IconButton>
              )
              : (<IconButton aria-label="close" onClick={() => this.props.handlePreview(file.fileId)}
                        className={'document-icon-button'}>
                <Visibility/>
              </IconButton>)
          }
          {
            userId === file.createdBy &&
            (<IconButton aria-label="close" onClick={() => this.props.handleRemove(file.fileId)}
                        className={'document-icon-button'}>
              <DeleteOutlined/>
            </IconButton>)
          }
          </div>}
        </Button>
    </div>
    )
  }
}
