import React, { Component } from 'react';
import './Consult.css';
import ConsultForm from "../ConsultForm/ConsultForm";
import { AppBar, Button, IconButton, Snackbar, Toolbar, Typography } from "@material-ui/core";
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import Video from "../Video/Video";
import { endConsult, getConsultInfo, startConsult, pauseConsult } from "../../ConsultApiActions";
import { connect } from "react-redux";
import MessageModal from "../../../../utils/components/MessageModal/MessageModal";
import { Redirect } from "react-router";

import Constants from '../../../../utils/Constants';
import ConstantMessages from '../../../../utils/Strings';
import { clearSnackbar, showSnackbar } from "../../../../utils/components/AppLoader/AppLoaderActions";
import { Alert } from "@material-ui/lab";
import AppLoader from "../../../../utils/components/AppLoader/AppLoader";

class Consult extends Component {
  constructor(props) {
    super(props);

    this.state = {
      consult: {}
    }
  }

  componentDidMount() {
    const consultId = sessionStorage.getItem('consult');
    if (consultId != null) {
      this.props.getConsultInfo(consultId).then(res => {
        this.setState({ consult: res.data[0] });
      }, error => {
        this.props.showSnackbar(ConstantMessages.somethingWentWrong, Constants.SNACKBAR_VARIAINT.ERROR);
      });
    }
  }

  showConfirmationForExit = () => {
    this.refs.messageModal.setState({
      showModal: true, message: 'Are you sure, you want to leave this consult?', title: 'Pause consult',
      showOkButton: true,
      showCancelButton: true,
      handleOk: () => {
      },
      handleCancel: () => {
        const consultId = sessionStorage.getItem('consult');
        this.props.pauseConsult(consultId).then(res => {
          this.props.history.goBack();
        }, error => {
          this.props.showSnackbar(ConstantMessages.somethingWentWrong, Constants.SNACKBAR_VARIAINT.ERROR);
        })
      },
      okText: 'No',
      cancelText: 'Yes'
    });
  };

  showConfirmationForEnd = () => {
    this.refs.messageModal.setState({
      showModal: true, message: 'Are you sure, you want to end this consult?', title: 'End consult',
      showOkButton: true,
      showCancelButton: true,
      handleOk: () => {
      },
      handleCancel: () => {
        const consultId = sessionStorage.getItem('consult');
        this.props.endConsult(consultId).then(res => {
          sessionStorage.removeItem('consult');
          this.props.history.goBack();
        }, error => {
          this.props.showSnackbar(ConstantMessages.somethingWentWrong, Constants.SNACKBAR_VARIAINT.ERROR);
        })
      },
      okText: 'No',
      cancelText: 'Yes'
    });
  };

  /**
   * Method used to close the snackbar
   */
  handleCloseSnackbar = () => {
    this.props.clearSnackbar();
  };

  openDialog = (chiefComplaint) => {
    this.refs.messageModal.setState({
      showModal: true,
      message: chiefComplaint, title: 'Chief complaint',
      showOkButton: true,
      handleOk: () => {
      },
      okText: 'Ok',
    });
  }

  renderChiefComplaint = (chiefComplaintData) => {
    let chiefComplaint = "";
    if (chiefComplaintData.length > 50) {
      const subStr = chiefComplaintData.substr(0, 50);
      chiefComplaint = subStr;
      return (
        <span>
          <span>{chiefComplaint}</span>
          <span className="moreButton" onClick={() => this.openDialog(chiefComplaintData)}>...more</span>
        </span>
      )
    } else {
      return (chiefComplaintData)
    }
  }

  render() {
    const consultId = sessionStorage.getItem('consult');
    const { consult } = this.state;

    if (consultId === null) {
      return <Redirect to={'/dashboard'} />
    }
    return (
      <>
        <AppBar
          color="inherit"
          position="fixed">
          <Toolbar>
            <Typography variant="h6" noWrap className="sideMenuName">
              {
                consult.patientDetails
                  ? (
                    <>
                      <span>{consult.patientDetails.name}</span>
                      <span> ({this.renderChiefComplaint(consult.patientDetails.chiefComplaint)})</span>
                    </>
                  )
                  : <></>
              }
              {/* {consult.patientDetails && (`${consult.patientDetails.name} (${this.renderChiefComplaint(consult.patientDetails.chiefComplaint)})`)} */}
            </Typography>
            <Button variant="contained" color="secondary" className={'suspend-consult-button'}
              onClick={this.showConfirmationForExit}>Pause</Button>
            <Button variant="contained" color="secondary" className={'end-consult-button'}
              onClick={this.showConfirmationForEnd}>End</Button>
          </Toolbar>
        </AppBar>
        <main className={'consult-content'}>
          <div>
            <div className={'consult-grid'}>
              <Video sessionId={consult.sessionId} token={consult.doctorToken} />
            </div>
            <div className={'consult-grid'}>
              <ConsultForm consultId={consultId} />
            </div>
          </div>
        </main>

        <MessageModal
          ref="messageModal"
        />

        <Snackbar open={this.props.showSnackbar} autoHideDuration={3000}
          onClose={() => this.handleCloseSnackbar()}
          anchorOrigin={{ vertical: 'top', horizontal: 'right' }}>
          <Alert severity={this.props.snackbarVariant}>
            {this.props.snackbarMessage}
          </Alert>
        </Snackbar>

        <AppLoader showLoader={this.state.showLoader} />
      </>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    showSnackbar: state.showLoader.showSnackbar,
    snackbarVariant: state.showLoader.snackbarVariant,
    snackbarMessage: state.showLoader.snackbarMessage
  }
};

const mapDispatchToProps = (dispatch) => {
  return {
    getConsultInfo: consultId => {
      return dispatch(getConsultInfo(consultId)).then(res => {
        return Promise.resolve(res);
      }, error => {
        return Promise.reject(error);
      });
    },

    endConsult: consultId => {
      return dispatch(endConsult(consultId)).then(res => {
        return Promise.resolve(res);
      }, error => {
        return Promise.reject(error);
      });
    },

    pauseConsult: consultId => {
      return dispatch(pauseConsult(consultId)).then(res => {
        return Promise.resolve(res);
      }, error => {
        return Promise.reject(error);
      });
    },

    showSnackbar: (message, variant) => {
      dispatch(showSnackbar(message, variant));
    },
    clearSnackbar: () => {
      dispatch(clearSnackbar())
    },
  }
};

Consult = connect(
  mapStateToProps,
  mapDispatchToProps
)(Consult);

export default Consult;
