class ConsultCard extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            pendingConsult: 0,
            practiceUser: 0,
            revenue: 0,
            upcomingConsult: 0,
            historyConsult: 0,
            isCoordinatorOnly: false,
            isPracticeAdminOnly: false
        };
    }

    /**
     * Get dashboard summary for practice/doctor method call when component is render
     * AttachComponent method call when component is render
     */
    componentDidMount = () => {
        if (this.props.loggedInUser && this.props.loggedInUserPractice) {
            if (this.props.currentRole.isPracticeAdmin || this.props.currentRole.isCoordinator) {
                this.getPracticeDashboardSummary(this.props.loggedInUserPractice.PracticeId);
            } else if (this.props.currentRole.isDoctor) {
                this.getDoctorDashboardSummary(this.props.loggedInUserPractice.userId);
            }
        }

        if (this.props.currentRole.isCoordinator && !this.props.currentRole.isPracticeAdmin && !this.props.currentRole.isDoctor) {
            this.setState({
                isCoordinatorOnly: true
            })
        }

        if (this.props.currentRole.isPracticeAdmin && !this.props.currentRole.isCoordinator && !this.props.currentRole.isDoctor) {
            this.setState({
                isPracticeAdminOnly: true
            })
        }
    };

    /**
     * Get dashboard summary for practice/doctor method call when component recive new props
     * AttachComponent method call when component recive new props
     */
    componentWillReceiveProps = (nextProps) => {
        if (nextProps.loggedInUser && nextProps.loggedInUserPractice !== this.props.loggedInUserPractice) {
            if (nextProps.currentRole.isPracticeAdmin || nextProps.currentRole.isCoordinator) {
                this.getPracticeDashboardSummary(nextProps.loggedInUserPractice.PracticeId);
            } else if (nextProps.currentRole.isDoctor) {

                this.getDoctorDashboardSummary(nextProps.loggedInUserPractice.userId);
            }
        }

        if (nextProps.isRefreshCount) {
            if (nextProps.currentRole.isPracticeAdmin || nextProps.currentRole.isCoordinator) {
                this.getPracticeDashboardSummary(nextProps.loggedInUserPractice.PracticeId);
            } else if (nextProps.currentRole.isDoctor) {

                this.getDoctorDashboardSummary(nextProps.loggedInUserPractice.userId);
            }

            nextProps.callBackCounts();
        }
    }

    /**
     * Method is used for get dashboard summary/stats for practice admin/co-ordinator
     */
    getPracticeDashboardSummary = (practiceId) => {
        this.props.getPracticeDashboardSummary(practiceId).then((res) => {
            if (res) {

                this.setState({
                    pendingConsult: res.pendingConsult ? res.pendingConsult : 0,
                    practiceUser: res.practiceUser ? res.practiceUser : 0,
                    revenue: res.revenue ? res.revenue : 0,
                    historyConsult: res.historyConsult ? res.historyConsult : 0,
                    upcomingConsult: res.upcomingConsult ? res.upcomingConsult : 0
                })
            }
        })
    }

    /**
     * Method is used for get dashboard summary/stats for doctor
     */
    getDoctorDashboardSummary = (practiceId) => {
        this.props.getDoctorDashboardSummary(practiceId).then((res) => {
            if (res) {
                this.setState({
                    pendingConsult: res.pendingConsult ? res.pendingConsult : 0,
                    practiceUser: res.practiceUser ? res.practiceUser : 0,
                    historyConsult: res.historyConsult ? res.historyConsult : 0,
                    revenue: res.revenue ? res.revenue : 0,
                    upcomingConsult: res.upcomingConsult ? res.upcomingConsult : 0
                })
            }
        })
    }

    /**
     * Method is used for render the dashboard cards based on authorization
     */
    consultsCardContent = () => {
        const { className, classes, currentRole, loggedInUserPractice, selectedCard } = this.props;
        const { isCoordinatorOnly } = this.state;
        return (
            <>
                {
                    !currentRole.isPracticeAdmin
                        ? (
                            <Grid item lg={(!loggedInUserPractice.Practice.allowPayment || isCoordinatorOnly) ? 4 : 3} sm={6} xl={(!loggedInUserPractice.Practice.allowPayment || isCoordinatorOnly) ? 4 : 3} xs={12} className="consult-card-padding-0">
                                <Card className={`${clsx(classes.root, className)} ${selectedCard === "pending"?'activeCard':''}`} onClick={() => selectedCard === "pending"?this.props.onCardDeselect():this.props.onCardSelect("pending")}>
                                    <CardContent>
                                        <Grid container spacing={2} justify="flex-start">
                                            <Grid item>
                                                {/* <Avatar className={classes.avatar}> */}
                                                <SvgIcon className="card-icon">
                                                    <RequestQueyeIcon color="primary" />
                                                </SvgIcon>
                                                {/* </Avatar> */}
                                            </Grid>
                                            <Grid item className='cardTextContainer'>
                                                <Typography className="card-title">{this.state.pendingConsult || 0}</Typography>
                                                <Typography className="card-text-title"
                                                    color="textSecondary" gutterBottom variant="body2">
                                                    Pending Consults
                                                </Typography>
                                            </Grid>
                                        </Grid>
                                    </CardContent>
                                </Card>
                            </Grid>
                        )
                        : <></>
                }

                <Grid item lg={(!loggedInUserPractice.Practice.allowPayment || isCoordinatorOnly) ? 4 : 3} sm={6} xl={(!loggedInUserPractice.Practice.allowPayment || isCoordinatorOnly) ? 4 : 3} xs={12}  className="consult-card-padding-0">
                    <Card className={`${clsx(classes.root, className)} ${selectedCard === "upcoming"?'activeCard':''}`} onClick={() => selectedCard === "upcoming"?this.props.onCardDeselect():this.props.onCardSelect("upcoming")}>
                        <CardContent>
                            <Grid container spacing={2} justify="flex-start">
                                <Grid item>
                                    <SvgIcon className="card-icon">
                                        <UpcomingConsultIcon />
                                    </SvgIcon>
                                </Grid>
                                <Grid item className='cardTextContainer'>
                                    <Typography className="card-title">{this.state.upcomingConsult || 0}</Typography>
                                    <Typography className="card-text-title"
                                        color="textSecondary" gutterBottom variant="body2">
                                        Upcoming Consults
                                    </Typography>
                                </Grid>
                            </Grid>
                        </CardContent>
                    </Card>
                </Grid>
                <Grid item lg={(!loggedInUserPractice.Practice.allowPayment || isCoordinatorOnly) ? 4 : 3} sm={6} xl={(!loggedInUserPractice.Practice.allowPayment || isCoordinatorOnly) ? 4 : 3} xs={12}  className="consult-card-padding-0">
                    <Card className={`${clsx(classes.root, className)} ${selectedCard === "historical"?'activeCard':''}`} onClick={() => selectedCard === "historical"?this.props.onCardDeselect():this.props.onCardSelect("historical")}>
                        <CardContent>
                            <Grid container spacing={2} justify="flex-start">
                                <Grid item>
                                    <SvgIcon className="card-icon">
                                        <HistoricalConsultIcon />
                                    </SvgIcon>
                                </Grid>
                                <Grid item className='cardTextContainer'>
                                    <Typography className="card-title">{this.state.historyConsult}</Typography>
                                    <Typography className="card-text-title"
                                        color="textSecondary" gutterBottom variant="body2">
                                        Historical Consults
                                    </Typography>
                                </Grid>
                            </Grid>
                        </CardContent>
                    </Card>
                </Grid>
                {
                    currentRole.isPracticeAdmin ? (
                        <Grid item lg={!loggedInUserPractice.Practice.allowPayment ? 4 : 3} sm={6} xl={!loggedInUserPractice.Practice.allowPayment ? 4 : 3} xs={12}  className="consult-card-padding-0">
                            <Card className={`${clsx(classes.root, className)} ${selectedCard === "users"?'activeCard':''}`} onClick={() => selectedCard === "users"?this.props.onCardDeselect():this.props.onCardSelect("users")}>
                                <CardContent>
                                    <Grid container spacing={2} justify="flex-start">
                                        <Grid item>
                                            <SvgIcon className="card-icon">
                                                <PeopleIcon color="primary" />
                                            </SvgIcon>
                                        </Grid>
                                        <Grid item className='cardTextContainer'>
                                            <Typography className="card-title">{this.state.practiceUser}</Typography>
                                            <Typography className="card-text-title"
                                                color="textSecondary" gutterBottom variant="body2">
                                                Practice Users
                                            </Typography>
                                        </Grid>
                                    </Grid>
                                </CardContent>
                            </Card>
                        </Grid>
                    )
                        : <></>
                }

                {
                    !this.state.isCoordinatorOnly && loggedInUserPractice.Practice.allowPayment
                        ? (
                            <>
                                <Grid item lg={!loggedInUserPractice.Practice.allowPayment ? 4 : 3} sm={6} xl={!loggedInUserPractice.Practice.allowPayment ? 4 : 3} xs={12}  className="consult-card-padding-0">
                                    <Card className={clsx(classes.rootRevenue, className)}>
                                        <CardContent>
                                            <Grid container spacing={2} justify="flex-start">
                                                <Grid item>
                                                    <SvgIcon className="card-icon" color="default">
                                                        <AttachMoneyIcon color="default" />
                                                    </SvgIcon>
                                                </Grid>
                                                <Grid item className='cardTextContainer'>
                                                    <Typography className={classes.title} color="inherit" variant="h3">{this.props.loggedInUserPractice.Practice.currency} {this.state.revenue}</Typography>
                                                    <Typography className={classes.title} color="inherit"
                                                        gutterBottom variant="body2">
                                                        Revenue
                                                    </Typography>
                                                </Grid>
                                            </Grid>
                                        </CardContent>
                                    </Card>
                                </Grid>
                            </>
                        )
                        : <></>
                }
            </>
        )
    }

    render() {
        return (
            <Grid container spacing={4} className="consult-card-container">
                {this.consultsCardContent()}
            </Grid>
        )
    }

}

const mapStateToProps = state => {
    return {
        loggedInUser: state.auth.loggedInUser,
        currentRole: state.auth.currentRole,
        loggedInUserPractice: state.auth.loggedInUserPractice
        //   stats: state.stats.stats
    }
};

const mapDispatchToProps = (dispatch) => {
    return {
        getPracticeDashboardSummary: (practiceId) => {
            return dispatch(getPracticeSummary(practiceId)).then((res) => {
                return Promise.resolve(res);
            }, (error) => {
                return Promise.reject(error);
            });

        },
        getDoctorDashboardSummary: (practiceId) => {
            return dispatch(getDoctorSummary(practiceId)).then((res) => {
                return Promise.resolve(res);
            }, (error) => {
                return Promise.reject(error);
            });

        }
    }
};

ConsultCard = connect(
    mapStateToProps,
    mapDispatchToProps
)(ConsultCard);

export default withStyles(styles)(ConsultCard);
