import React from 'react';
import './SubscriptionPlan.css';
import { connect } from 'react-redux';
import { withStyles } from '@material-ui/core/styles';

import { Grid } from '@material-ui/core';

import {
    getAllSubscriptionPlansRequest
} from '../SubscriptionPlansApiActions';
import { setPageTitle } from "../../Dashboard/DashboardActions";

import { getUserPracticeRequest } from '../../User/UserApiActions';
import { getConnectAccountRequest } from "../SubscriptionPlansApiActions";

import SetupPracticeStepThree from '../../Practice/components/SetupPractice/SetupPracticeStepThree';
import SetupPracticeStepTwo from '../../Practice/components/SetupPractice/SetupPracticeStepTwo';
import StripeDashboard from './StripeDashboard';

const styles = theme => ({
    root: {
        flexGrow: 1,
    },
    paper: {
        padding: theme.spacing(2),
        color: theme.palette.text.secondary,
    }
});

class SubscriptionPlans extends React.Component {

    /**
     * Set page title when component is render
     * Get logged user practice details when component is render
     * Get stripe connect account details of the logged in user when component is render
     * Get all subscription details when component is render is subscription flag is true in logeed in user details
     * AttachComponent method call when component is render
     */
    componentDidMount = () => {
        sessionStorage.setItem('redirectUrl', '/account');
        this.props.updatePageTitle("Account", false);
        this.props.getUserPractice(this.props.loggedInUser.id).then((res) => {
            if (res) {
                this.props.getConnectAccount(res.PracticeId);
                if (this.props.loggedInUser.isSubscription) {
                    this.props.getAllSubscriptionPlans();
                }
            }
        });

    }

    render() {
        const { classes, loggedInUser, loggedInUserPractice } = this.props;
        return (
            <Grid container spacing={3}>
                {
                    loggedInUserPractice.Practice.allowPayment && !loggedInUserPractice.Practice.stripeConnectAccountId
                        ? (
                            <Grid item xs={12} md={12} className={classes.paper}>
                                <SetupPracticeStepTwo />
                            </Grid>
                        )
                        : loggedInUser.isSubscription && loggedInUserPractice.Practice.allowPayment && loggedInUserPractice.Practice.stripeConnectAccountId
                            ? (
                                <>
                                    <Grid item xs={12} md={9} className={classes.paper}>
                                        <SetupPracticeStepThree />
                                    </Grid>
                                    <Grid item xs={12} md={3} className="">
                                        <Grid container spacing={3}>
                                            <Grid item xs={12} className="informationHeader subscriptionTitle">
                                                Connect Account
                                    </Grid>
                                            <Grid item xs={12} className="informationHeader subscriptionTitle">
                                                <StripeDashboard />
                                            </Grid>
                                        </Grid>
                                    </Grid>
                                </>
                            )
                            : loggedInUser.isSubscription
                                ? (
                                    <Grid item xs={12} md={12} className={classes.paper}>
                                        <SetupPracticeStepThree />
                                    </Grid>
                                )
                                : loggedInUserPractice.Practice.allowPayment && loggedInUserPractice.Practice.stripeConnectAccountId
                                    ? (
                                        <>
                                            <Grid item xs={12} md={4} className="">
                                            </Grid>
                                            <Grid item xs={12} md={4} className="">
                                                <Grid container spacing={3}>
                                                    <Grid item xs={12} className="informationHeader subscriptionTitle">
                                                        Connect Account
                                    </Grid>
                                                    <Grid item xs={12} className="informationHeader subscriptionTitle">
                                                        <StripeDashboard />
                                                    </Grid>
                                                </Grid>
                                            </Grid>
                                            <Grid item xs={12} md={4} className="">
                                            </Grid>
                                        </>
                                    )
                                    : <></>
                }
            </Grid>
        )
    }

}

const mapStateToProps = (state) => {
    return {
        loggedInUser: state.auth.loggedInUser,
        currentRole: state.auth.currentRole,
        loggedInUserPractice: state.auth.loggedInUserPractice,
        subscriptions: state.subscriptions.subscriptions
    }
};

const mapDispatchToProps = (dispatch) => {
    return {
        updatePageTitle: (pageTitle, enablePageTitle) => {
            dispatch(setPageTitle(pageTitle, enablePageTitle));
        },
        getAllSubscriptionPlans: () => {
            return dispatch(getAllSubscriptionPlansRequest()).then((res) => {
                return Promise.resolve(res);
            }, (error) => {
                return Promise.reject(error);
            });
        },
        getUserPractice: (userId) => {
            return dispatch(getUserPracticeRequest(userId)).then((res) => {
                return Promise.resolve(res);
            }, (error) => {
                return Promise.reject(error);
            });
        },
        getConnectAccount: (id) => {
            return dispatch(getConnectAccountRequest(id)).then((res) => {
                return Promise.resolve(res);
            }, (error) => {
                return Promise.reject(error);
            });
        }
    }
}
SubscriptionPlans = connect(
    mapStateToProps,
    mapDispatchToProps
)(SubscriptionPlans);

export default withStyles(styles)(SubscriptionPlans);