import React from 'react';

import { Button, Dialog, DialogContent, DialogActions, DialogTitle } from '@material-ui/core';

/**
 * User table component which shows all users data
 */
class MessageModal extends React.Component {

  closeModal = () => {
    this.setState({ showModal: false });
  };

  constructor(props) {
    super(props);

    this.state = {
      showModal: false,
      message: '',
      title: '',
      showOkButton: false,
      showCancelButton: false,
      handleOk: null,
      handleCancel: null,
      okText: 'OK',
      cancelText: 'Cancel'
    }
  }

  componentDidLoad() {
  }

  render() {
    let footerButtons = [];

    if (this.state.showCancelButton) {
      footerButtons.push(
        <Button key="cancel" variant="contained" onClick={() => {
          this.setState({ showModal: false }, () => {
            if (this.state.handleCancel !== null)
              this.state.handleCancel();
          });
        }}>
          {this.state.cancelText}
        </Button>);
    }

    if (this.state.showOkButton) {
      footerButtons.push(
        <Button key="ok" variant="contained" color="primary" onClick={() => {
          this.setState({ showModal: false }, () => {
            if (this.state.handleOk !== null)
              this.state.handleOk();
          });
        }}>
          {this.state.okText}
        </Button>);
    }

    return (
      <Dialog
        open={this.state.showModal}
        // maxWidth="md"
        aria-labelledby="responsive-dialog-title"
      >
        <DialogTitle id="responsive-dialog-title">{this.state.title}</DialogTitle>
        <DialogContent style={{ textAlign: 'justify' }}>
          {this.state.message}
        </DialogContent>
        <DialogActions>
          {footerButtons}
        </DialogActions>
      </Dialog>
    )
  }

}

export default MessageModal;