import React from 'react';
import { connect } from 'react-redux';
import { Grid, Button, Avatar } from '@material-ui/core';

import MaterialTable from 'material-table';
import { ROW_PER_PAGE_5, SYSTEM_ROLES } from '../../../../../utils/Constants';

import { setPageTitle } from "../../../DashboardActions";

import {
  getAllUpcomingConsultsRequest, startConsult
} from '../../../../Consult/ConsultApiActions';

import { getPracticeSummary, getDoctorSummary } from "../../../DashboardApiActions";
import Constants from '../../../../../utils/Constants';
import ConstantMessages from '../../../../../utils/Strings';
import { showSnackbar } from "../../../../../utils/components/AppLoader/AppLoaderActions";

import SvgIcon from '@material-ui/core/SvgIcon';
import { ReactComponent as StartIcon } from '../../../../../assets/svg/start.svg';
import {ReactComponent as InviteUserIcon} from "../../../../../assets/svg/add_user.svg";

class UpcomingConsults extends React.Component {

  constructor(props) {
    super(props);
    this.paginationParams = {
      page: 0,
      rowsPerPage: ROW_PER_PAGE_5
    };
    this.state = {
      paginationParams: Object.assign({}, this.paginationParams),
      isRefresh: false
    }
  };

  /**
   * Set page title action call
   * Get all upcoming consults method call when component is render
   * AttachComponent method call when component is render
   */
  componentDidMount = () => {
    this.props.updatePageTitle("Upcoming Consults", false);
    if (this.props.loggedInUserPractice) {
      let data = this.state.paginationParams;
      this.props.getAllUpcomingConsults(data);
    }
  }

  /**
   * Method is execute when componet is update
   * AttachComponent method call when component is render
   */
  componentWillUpdate = () => {
    const { isRefresh } = this.state;
    if (isRefresh) {
      this.setState({
        isRefresh: false
      }, () => {
        if (this.props.loggedInUserPractice) {
          let data = this.state.paginationParams;
          this.props.getAllUpcomingConsults(data);
        }
      })
    }
  }

  render() {
    const { upcomingConsult } = this.props;
    const { paginationParams } = this.state;

    const columns = [{
      title: 'Name',
      fiels: 'name',
      filterable: false,
      sorting: false,
      render: props => {
        if(props.patientDetails && props.patientDetails.name){
          var array1 = props.patientDetails.name.split(' ');
          var newarray1 = '';
          for(var x = 0; x < array1.length; x++){
            if(x < 3){
              newarray1=newarray1+array1[x].charAt(0).toUpperCase();
            }
          }
          return (
            <Grid container spacing={1}>
              <Grid item xs={2}>
                <Avatar>{ newarray1 }</Avatar>
              </Grid>
              <Grid item xs={10} className="verticalAlignMiddle">
                <span>{props.patientDetails ? props.patientDetails.name : "-"}</span>
              </Grid>
            </Grid>
          )
        }else{
          return "-";
        }
      }
    },
    {
      title: 'Consult Time',
      field: 'appointmentDate',
      filterable: false,
      sorting: false,
      render: props => {
        let displayDate = props.appointmentTime ? props.appointmentTime : '-' //moment(new Date(props.appointmentDate)).format("dddd,  DD MMMM h:mm a z");
        return (
          <span>
            {displayDate}
          </span>
        )
      }
    },
    {
      title: 'Doctor Name',
      field: 'doctorName',
      filterable: false,
      sorting: false,
      hidden: !this.props.loggedInUser.Roles.find((role) => role.name === SYSTEM_ROLES.ROLE_COORDINATOR),
      render: props => {
        return (
          <span>
            {props.doctorName ? props.doctorName : '-'}
          </span>
        )
      }
    },
    {
      title: 'Action',
      maxWidth: 150,
      filterable: false,
      sorting: false,
      field: 'id',
      sortable: false,
      hidden: !this.props.loggedInUser.Roles.find((role) => role.name === SYSTEM_ROLES.ROLE_DOCTOR),
      render: props => {
        return (
          <div>
            {
              <Button size="small" variant="outlined" className="start-consult-button" onClick={() => this.startConsult(props.id)}>
                <SvgIcon className="marginRight5">
                  <StartIcon color="primary" />
                </SvgIcon>
                Start
              </Button>
            }
          </div>
        )
      }
    }
    ];

    let isDoctor = this.props.loggedInUser.Roles.find((role) => role.name === SYSTEM_ROLES.ROLE_DOCTOR);
    if (isDoctor) {
      columns.push()
    }
    return (
      <Grid container spacing={3}>
        <Grid item xs={12}>
          <MaterialTable
            title={
              <Grid item xs={12} className="table-header-title-container">
                <span className="table-header-title">Upcoming Consults</span>
              </Grid>
            }
            scrollable
            columns={columns}
            data={upcomingConsult.rows}
            options={{
              search: false,
              pageSizeOptions: [10, 20, 30, 40, 50],
              pageSize: paginationParams.rowsPerPage,
              emptyRowsWhenPaging: false,
              filtering: false,
              headerStyle: {
                padding: '2px 40px 2px 16px'
              },
              searchFieldStyle: {
                padding: '2px 40px 2px 16px'
              }
            }}
            pages={Math.ceil(upcomingConsult.count / paginationParams.rowsPerPage)}
            totalCount={upcomingConsult.count}
            page={paginationParams.page}
            onChangeRowsPerPage={(pageSize) => {
              let data = this.state.paginationParams;
              data['rowsPerPage'] = pageSize;
              this.setState({
                paginationParams: data
              }, () => {
                this.props.getAllUpcomingConsults(data);
              })
            }}
            onChangePage={(page) => {
              let data = this.state.paginationParams;
              data['page'] = page;
              this.setState({
                paginationParams: data
              }, () => {
                this.props.getAllUpcomingConsults(this.state.paginationParams);
              })

            }}
          />
        </Grid>
      </Grid>
    )
  }

  startConsult = consultId => {
    this.props.startConsult(consultId).then(res => {
      this.setState({
        isRefresh: true
      }, () => {
        sessionStorage.setItem('consult', consultId);
        window.location = '/consult';
      });
    }, error => {
      this.props.showSnackbar(ConstantMessages.somethingWentWrong, Constants.SNACKBAR_VARIAINT.ERROR);
    });
  }
}

const mapStateToProps = state => {
  return {
    loggedInUser: state.auth.loggedInUser,
    currentRole: state.auth.currentRole,
    upcomingConsult: state.consults.upcomingConsults,
    loggedInUserPractice: state.auth.loggedInUserPractice,
  }
};

const mapDispatchToProps = (dispatch) => {
  return {
    startConsult: consultId => {
      return dispatch(startConsult(consultId)).then(res => {
        return Promise.resolve(res);
      }, error => {
        return Promise.reject(error);
      });
    },

    getAllUpcomingConsults: (data) => {
      return dispatch(getAllUpcomingConsultsRequest(data)).then((res) => {
        return Promise.resolve(res);
      }, (error) => {
        return Promise.reject(error);
      });
    },


    getPracticeDashboardSummary: (practiceId) => {
      return dispatch(getPracticeSummary(practiceId)).then((res) => {
        return Promise.resolve(res);
      }, (error) => {
        return Promise.reject(error);
      });

    },
    getDoctorDashboardSummary: (practiceId) => {
      return dispatch(getDoctorSummary(practiceId)).then((res) => {
        return Promise.resolve(res);
      }, (error) => {
        return Promise.reject(error);
      });
    },
    showSnackbar: (message, variant) => {
      dispatch(showSnackbar(message, variant));
    },
    updatePageTitle: (pageTitle, enablePageTitle) => {
      dispatch(setPageTitle(pageTitle, enablePageTitle));
    }
  }
};

UpcomingConsults = connect(
  mapStateToProps,
  mapDispatchToProps
)(UpcomingConsults);

export default UpcomingConsults;
